import { Component, HostListener, OnInit, Output } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';
import { data } from './data';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  collapsed: boolean;
  scroll: boolean;
  // data for slide component
  slides;
  // data for about us component
  vmv;
  content;
  //data for services
  services;
  team;
  date = new Date().getFullYear();



  //data for services component

  constructor(d: data) {
    this.slides = d.slides;
    this.vmv = d.vmv;
    this.content = d.content;
    this.services = d.services;
    this.team = d.team;
  }

  ngOnInit() {
    if (window.innerWidth < 671) {
      this.collapsed = true;
    }

    if( window.pageYOffset > 150){
      this.scroll = true;
    } else this.scroll = false
  }

  @HostListener('window:resize', ['$event']) resizer(e) {
    if (window.innerWidth < 670) {
      this.collapsed = true;
    } else {
      this.collapsed = false;
    }
  }

  @HostListener('window:scroll', ['$event']) scrollspy(e) {
    if( window.pageYOffset > 150) {
      this.scroll = true;
    } else this.scroll = false
  }

}



