import { Component, Input } from '@angular/core';

@Component({
  selector: 'app-team',
  templateUrl: './team.component.html',
  styleUrls: ['./team.component.css']
})
export class TeamComponent {
  @Input() team;

  constructor() { }

  formatlink(link): string {
    let e = link.toLowerCase();
    if(e[0] === 'h' && e[1] === 't') {
        return e;
    } else {
      return `http://${e}`;
    }
  }

}



