import { Component, OnInit, Input } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';

@Component({
    selector: `image-slider`,
    templateUrl: `./image-slider.component.html`,
    styleUrls: [`./image-slider.component.css`],
    animations: [
        trigger('inout', [
            state('in', style({
                opacity: '1',
                'z-index': '2'
            })),
            state('out', style({
                opacity: '0.5'
            })),
            transition('void => *', [style({transform: 'translateX(-100%)'}), animate('500ms ease-in')]),
            transition('* => void', [animate('500ms ease-in', style({transform: 'translateX(100%)'}))])
        ]),

        trigger('rtl', [
            state('in', style({
                opacity: 1,
                transform: 'translateX(0)'
            })),
            transition('void => *', [style({transform: 'translateX(100%)', opacity: 0}), animate('500ms 650ms ease-in')])
        ]),

        trigger('ltr', [
            state('in', style({
                opacity: 1,
                transform: 'translateX(0)'
            })),
            transition('void => *', [style({transform: 'translateX(-100%)', opacity: 0}), animate('500ms 650ms ease-in')])
        ])
    ]
})

export class ImageSlider implements OnInit{
    // slides is expected to be an array of objects with 
    // a link, a paragraph 
    // a button text
    // an image
    @Input() slides;
    @Input() timer;
    called: boolean;
    index = 0;
    anime = 'in';

    constructor(){}

    ngOnInit(){
        setInterval(
            () => {
                if(this.called){
                    clearInterval(2000);
                    this.called = false;
                } else
                {
                    this.right();
                }
            }, this.timer );
    }


    right() {
        if(this.index + 1 >= this.slides.length){
            this.index = 0;
        }  else {
            this.index += 1;
        }
    }

    left() {
        if(this.index - 1 < 0){
            this.index = this.slides.length - 1;
        }  else {
            this.index -= 1;
        }
    }


    urlmaker(url: string): String {
        return `url(${url})`;
    }
}


